.features {
  @include r(1023) {
    display: block;
    height: 100%important; }
  &__wrapper {
    @include r(1023) {
      display: block;
      height: 100%; } }
  .container {
    @include r(1023) {
      height: 100%; } }
  &__container {
    padding: 130px 0 $gap-400;
    height: 100vh;
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: 1fr auto;
    @include b(1919) {
      padding: $gap-800 0 $gap-400; }
    @include b(609) {
      height: auto; }
    @include r(1023) {
      padding: $gap-800 0 $gap-500;
      grid-template-rows: 1fr 3fr;
      height: 100%; }
    @include r(767) {
      height: auto;
      grid-template-rows: auto 500px; } }
  &__content {
    width: 100%;
    height: 100%;
    position: relative; }
  &__slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    @include r(1023) {
      position: relative; } }
  &__slider-slide {
    display: flex;
    align-items: stretch;
    flex-shrink: 0;
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;
    padding-top: $gap-500;
    @include r(1439) {
      padding-top: $gap-200; }
    @include r(1023) {
      padding-top: 0;
      flex-direction: column; } }
  &__slider-slide-content {
    max-width: 600px;
    align-self: center;
    flex-shrink: 0;
    padding-right: $gap-250;
    @include r(1919) {
      max-width: 450px;
      padding: 0 $gap-500 0 0; }
    @include r(1279) {
      margin-bottom: $gap-300; }
    @include r(1199) {
      max-width: 400px; }
    @include r(1023) {
      max-width: 100%;
      padding-right: 0;
      margin-bottom: 0; }
    @include r(767) {
      width: 100%; }
    .block-title {
      margin-bottom: $gap-300;
      @include r(1023) {
        text-align: center; } }
    .block-subtitle {
      @include r(1023) {
        display: none; } } }
  &__slider-slide-content-icon {
    width: 150px;
    height: 150px;
    margin-bottom: $gap-300;
    @include r(1919) {
      width: 120px;
      height: 120px; }
    @include r(1439) {
      width: 100px;
      height: 100px; }
    @include r(1023) {
      display: none; } }
  &__slider-slide-media {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    @include r(479) {
      justify-content: space-around; } }
  &__slider-slide-media-image {
    position: relative;
    margin: 0 $gap-200;
    @include r(767) {
      margin: 0 $gap-100 0 0;
      &:last-child {
        margin: 0; } }
    .block-phone-container {
      width: 100%;
      height: 100%;
      position: relative;
      z-index: $z-index-1; }
    picture,
    img {
      height: 100%; }
    picture {
      position: relative;
      width: 320px;
      height: 100%;
      max-height: 730px;
      @include r(1919) {
        max-height: 550px;
        width: 250px; }
      @include b(699) {
        width: 230px; }
      @include r(1439) {
        max-height: 500px;
        width: 230px; }
      @include r(1023) {
        max-height: 450px;
        width: 210px; }
      @include r(767) {
        width: 150px;
        max-height: 350px; } }
    img {
      object-position: 50% 0%;
      object-fit: cover; } }
  &__slider-slide-media-bg {
    &.icon-1,
    &.icon-2,
    &.icon-3 {
      position: absolute;
      z-index: $z-index-invert; }
    &.icon-1 {
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: 100% 50%;
      @include r(1023) {
        object-position: 50% 50%; } }
    &.icon-2 {
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      @include r(1023) {
        top: 0;
        left: 0;
        transform: translate(0); } }
    &.icon-3 {
      top: 50%;
      right: 0;
      transform: translate(0, -50%); }
    &.icon-3 {} }
  &__navigation {
    padding-top: $gap-600;
    grid-row: 2 / 3;
    @include r(1919) {
      padding-top: $gap-250; }
    @include r(1023) {
      grid-row: 1 / 2;
      padding-top: $gap-150;
      margin-bottom: $gap-250; }
 }    // +r()
  &__navigation-list {
    display: flex;
    justify-content: space-between;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 4px;
      filter: opacity(10%);
      background-color: var(--features-bg-color); } }
  &__navigation-list-item {
    padding-top: $gap-200;
    position: relative;
    width: calc(100% / 8);
    opacity: 0;
    transition: opacity $transition-100;
    @include r(1919) {
      padding-top: $gap-100; }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-25%);
      width: 100%;
      height: 6px;
      background-color: var(--features-bg-color);
      border-radius: 10px; }
    @include r(1023) {
      display: none;
      &:nth-child(1) {
        &::before {
          transform: translate(0, -25%); } }
      &:nth-child(2) {
        &::before {
          transform: translate(100%, -25%); } }
      &:nth-child(3) {
        &::before {
          transform: translate(200%, -25%); } }
      &:nth-child(4) {
        &::before {
          transform: translate(300%, -25%); } }
      &:nth-child(5) {
        &::before {
          transform: translate(400%, -25%); } }
      &:nth-child(6) {
        &::before {
          transform: translate(500%, -25%); } }
      &:nth-child(7) {
        &::before {
          transform: translate(600%, -25%); } }
      &:nth-child(8) {
        &::before {
          transform: translate(700%, -25%); } } }
    &--active {
      opacity: 1;
      @include r(1023) {
        display: flex; } } }
  &__navigation-list-item-block {
    display: flex;
    align-items: center;
    @include r(1023) {
      width: 100vw; } }
  &__navigation-list-item-icon {
    width: 40px;
