.be-iconist {
  &__container {
    padding: $gap-700 0 $gap-350;
    width: 100%;
    display: inline-grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: minmax(50px, 5%) minmax(2em, 20%) 65% minmax(10%, 80px);
    justify-items: center;
    @include r(1365) {
      grid-template-rows: auto auto 1fr auto; }
    @include b(699) {
      padding: $gap-400 0;
      grid-template-rows: minmax(50px, 5%) minmax(2em, 30%) 55% minmax(10%, 80px); }
    @include r(1023) {
      grid-template-rows: auto auto 1fr auto; }
    @include r(767) {
      height: auto;
      grid-template-rows: auto 450px auto; } }
  &__logo {
    margin-bottom: 5px;
    @include r(1023) {
      margin-bottom: $gap-200; }
    @include r(767) {
      display: none; } }
  &__block-title {
    text-align: center;
    max-width: 800px;
    @include rb;
    font-size: rem(46);
    @include r(1919) {
      font-size: rem(42); }
    @include r(1439) {
      font-size: rem(32); }
    @include b(699) {
      font-size: rem(28); }
    @include r(1023) {
      font-size: rem(28); } }
  &__illustration {
    align-self: center;
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0; }
  &__illustration-main {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: stretch;
    justify-content: center;
    padding-top: $gap-500;
    @include r(1919) {
      padding-top: $gap-300; }
    @include r(1023) {
     padding-top: $gap-250; }
    picture,
    img {
      height: 100%; }
    picture {
      position: relative;
      height: 100%;
      width: 300px;
      max-height: 650px;
      @include r(1921) {
        max-height: 600px;
        width: 250px; }
      @include b(699) {
        width: 220px; }
      @include b(649) {
        width: 200px; }
      @include r(1439) {
        max-height: 500px; }
      @include r(1199) {
        width: 220px; } }
    img {
      object-position: 50% 0%;
      object-fit: cover; }
    .block-phone-container {
      z-index: $z-index-1;
      display: flex;
      align-items: center;
      justify-content: center; } }
  &__tile {
    position: absolute;
    top: 0;
    z-index: $z-index-0;
    width: calc( 6 * 95px + 5 * 45px + 180px);
    height: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    overflow: hidden;
    padding-bottom: $gap-400;
    @include r(1919) {
      width: calc( 6 * 85px + 5 * 25px + 140px);
      padding: $gap-150 0; }
    @include r(1023) {
      padding: 0 0 $gap-300; }
    &.tile-1 {
      right: calc(50% + 50px);
      padding-left: $gap-500;
      align-items: flex-start;
      @include r(1365) {
        align-items: center; }
      @include r(767) {
        align-items: flex-end; } }
    &.tile-2 {
      left: calc(50% + 50px);
      padding-right: $gap-500;
      align-items: flex-end;
      @include r(1365) {
        align-items: center; }
      @include r(767) {
        align-items: flex-start; }
      .be-iconist__tile-row {
        justify-content: flex-end;
        direction: rtl; } }
    .block-icon {
      transition: transform $transition-200; }
    .block-icon__icon {
      width: 95px;
      height: 95px;
      padding: $gap-150;
      @include r(1919) {
        width: 85px;
        height: 85px; }
      @include b(699) {
        width: 75px;
        height: 75px; }
      @include b(649) {
        width: 65px;
        height: 65px; }
      @include r(767) {
        width: 75px;
        height: 75px; } }
    .block-icon__icon--empty {
      padding: 0;
      border-radius: 30px; } }
  &__tile-row {
    display: grid;
    grid-template-columns: repeat( 6, 95px );
    gap: $gap-450;
    margin-bottom: $gap-450;
    @include r(1919) {
      gap: $gap-350;
      margin-bottom: $gap-350;
      grid-template-columns: repeat( 6, 85px ); }
    @include b(699) {
      gap: $gap-150;
      margin-bottom: $gap-150;
      grid-template-columns: repeat( 6, 75px ); }
    @include b(649) {
      gap: $gap-100; }
    @include r(767) {
      gap: $gap-100;
      margin-bottom: $gap-250; }
    &:nth-child(1) {
      .block-icon__icon {
        transform: rotate(-20deg);
        @include b(699) {
          transform: rotate(-12deg); } } }
    &:nth-child(2) {
      margin-left: - $gap-250;
      .block-icon__icon {
        transform: rotate(20deg);
        @include b(699) {
          transform: rotate(12deg); } } }
    &:nth-child(3) {
      .block-icon__icon {
        transform: rotate(-20deg);
        @include b(699) {
          transform: rotate(-12deg); } } }
    &:last-child {
      margin-bottom: 0; } }
  &__social {
    padding-top: $gap-250;
    // +r(1365)
 } }    //   padding-top: $gap-500
