.section {
  // opacity: 1
  &__wrapper {
    position: relative; }
  &__bg {
    position: absolute;
    z-index: $z-index-0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    pointer-events: none;
    background-color: transparent; }
  &__container {
    padding: 160px 0 120px;
    height: 100vh;
    @include r(1921) {
      padding: 130px 0 $gap-900; }
    @include r(1365) {
      padding: 130px 0 $gap-500; }
    @include b(699) {
      padding: $gap-800 0 $gap-400; }
    @include b(609) {
      height: auto; }
    @include r(767) {
      height: auto;
      padding: $gap-500 0; } } }
