.tired {
  &__container {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: 1.5fr 3fr;
    @include r(1921) {
      grid-template-rows: auto 1fr; } }
  &__title {
    align-self: center;
    text-align: center;
    margin-bottom: $gap-500;
    @include r(1921) {
      align-items: flex-start;
      margin-bottom: $gap-400; }
    @include r(1365) {
      margin-bottom: $gap-300; }
    @include r(767) {
      text-align: left; } }
  &__content {
    position: relative; }
  &__tile {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include b(699) {
      justify-content: center; }
    @include r(1439) {
      justify-content: center; }
    @include r(1023) {
      justify-content: flex-start;
      position: relative;
      overflow: auto;
      padding-bottom: $gap-200; } }
  &__tile-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 $gap-100;
    width: 250px;
    max-height: 650px;
    height: 100%;
    @include r(1921) {
      max-height: 550px; }
    @include r(1439) {
      max-height: 450px; }
    @include r(1199) {
      width: calc(100% / 5); }
    @include r(1023) {
      max-height: 500px;
      width: 220px; } }
  &__tile-item-row {
    @include r(1023) {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: $gap-200; } }
  &__tile-item-icon {
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto $gap-100;
    color: $blue-1000;
    @include r(1919) {
      height: 30px; }
    @include b(699) {
      height: 25px; }
    @include r(1023) {
      margin: 0 $gap-200 0 0;
      flex-shrink: 0; }
    img,
    svg {
      height: 100%; } }
  &__tile-item-title {
    font-size: rem(17);
    @include rb;
    text-align: center;
    margin-bottom: $gap-150;
    min-height: 2.5em;
    @include r(1023) {
      margin-bottom: 0;
      font-size: rem(14); } }
  &__tile-item-image {
    height: 100%;
    position: relative;
    @include r(1919) {
      width: 200px; }
    @include b(699) {
      width: 180px; }
    @include r(1199) {
      width: 100%;
      max-width: 180px; }
    @include r(1023) {
      height: 470px;
      width: 200px;
      max-width: initial; }
    picture {
      width: 100%;
      overflow: hidden; }
    picture,
    img {
      height: 100%; }
    img {
      object-position: 50% 0%;
      object-fit: cover; } } }

