.block-phone-container {
  padding: 0 $gap-50;
  &::before,
  &::after {
    content: "";
    position: absolute;
    background-color: $gray-100;
    z-index: $z-index-0;
    width: 2px; }
  &::before {
    top: 20%;
    left: calc(#{$gap-50} - 2px);
    height: 30px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px; }
  &::after {
    top: 15%;
    right: calc(#{$gap-50} - 2px);
    height: 25px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    box-shadow: 0 20px $gray-100, 0 55px $gray-100; }
  picture {
    width: 250px;
    border-radius: $gap-300;
    box-shadow: $shadow-1100;
    border: $gray-100 solid 8px;
    overflow: hidden;
    @include r(1023) {
      border: $gray-100 solid 5px;
      border-radius: $gap-200; }
    @include r(1023) {
      width: 230px; } }
  img {
    width: 100%; } }
