._404 {
  &__container {
    min-height: 100vh;
    padding: 150px 0 120px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    z-index: $z-index-1;
    @include r(1921) {
      padding: 150px 0 $gap-900; }
    @include r(1023) {
      padding: 250px 0 $gap-900; }
    @include r(767) {
      padding: 150px 0 $gap-450; } }
  &__content {
    max-width: 600px;
    width: 100%;
    @include r(1919) {
      max-width: 500px; } }
  &__title,
  &__subtitle,
  &__button-wrapper {
    opacity: 0;
    transform: translateY(50px); }
  &__title {
    margin-bottom: $gap-300;
    font-size: rem(84);
    @include rb;
    @include r(1919) {
      font-size: rem(64); }
    @include r(1365) {
      font-size: rem(44); } }
  &__subtitle {
    margin-bottom: $gap-600; }
  &__button {
    display: inline-block;
    width: auto;
    @include r(479) {
      width: 100%; } }
  &__bg {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: $z-index-0;
    width: 80vw;
    max-height: calc(100vh - 150px);
    height: 100%;
    transform: translate(25%, 0);
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    @include r(1023) {
      height: 400px;
      width: 100%;
      top: 0;
      left: 50%;
      justify-content: center;
      transform: translate(-50%, -25%); }
    @include r(767) {
      width: 600px;
      max-height: initial;
      transform: translate(-50%, -50%); }
    svg {
      width: 100%;
      height: 100%; }
    .question {
      opacity: 0; } } }
